import React from 'react';
let tu3= require('../images/3.jpg');
let tuf= require('../images/f.jpg');

class App1 extends React.Component {
  render() {
    return (
      <div>
				<div className="bn">
    			<div id="bg-image">
        		<img src={tu3} />
        	</div>
        </div>
    		<h1>个人简历</h1>
				<div className="col-md-12 col-sm-3 col-xs-12">
    			<div className="menu" id="fd">
            <div id="profile-img">
              <img id="lk" src={tuf} />
            </div>
            <ul className="box" id="p">
							<li className="t1" id="t1"></li>
							<li className="t2" id="t2"></li>
							<li className="t3" id="t3"></li>
							<li className="t4" id="t4"></li>
							<li className="t5" id="t5"></li>
						</ul>
					</div>
        </div>
      </div>
    );
  }
}

export default App1;



