		function a(ss){
			var top=$("body").scrollTop()
			if(top<ss){
				var int=setInterval(function(){
					var top=$("body").scrollTop()
					if(top>=ss){
						clearInterval(int)
					}else{
						$("body").scrollTop($("body").scrollTop()+5)
					}
				},1)
			}else{
				var int=setInterval(function(){
					var top=$("body").scrollTop()
					if(top<=ss){
						clearInterval(int)
					}else{
						$("body").scrollTop($("body").scrollTop()-5)
					}
				},1)
			}
		}

		t1.onclick=function(){
			a(0)
		}
